  padding-bottom: 2vh;
}
.menuBar ul{
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
  
  width: 100%;
   
}

.menuBar a { 
  font-size: calc(10px + 1.75vw);
  color: white; 
